/* Global responsive styles */

/* Add padding to bottom of dashboard pages to account for mobile navbar */
@media (max-width: 768px) {
  .dashboard-container {
    padding-bottom: 80px !important;
  }
  
  /* Improve table responsiveness */
  .responsive-table {
    display: block;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
  
  /* Adjust card layouts for small screens */
  .card-grid {
    grid-template-columns: 1fr !important;
  }
  
  /* Make forms full width on mobile */
  .form-container {
    width: 100% !important;
    padding-left: 1rem !important;
    padding-right: 1rem !important;
  }
  
  /* Fix button sizes on small screens */
  .btn-responsive {
    width: 100% !important;
    margin-top: 0.5rem !important;
    margin-bottom: 0.5rem !important;
  }
  
  /* Adjust headings for mobile */
  h1.responsive-heading {
    font-size: 1.75rem !important;
  }
  
  h2.responsive-heading {
    font-size: 1.5rem !important;
  }
  
  /* Fix modal sizes on mobile */
  .modal-container {
    width: 95% !important;
    margin: 0 auto !important;
  }
}

/* Add specific styles for very small screens */
@media (max-width: 480px) {
  .xs-hidden {
    display: none !important;
  }
  
  .xs-block {
    display: block !important;
    width: 100% !important;
  }
  
  /* Further reduce heading sizes */
  h1.responsive-heading {
    font-size: 1.5rem !important;
  }
  
  h2.responsive-heading {
    font-size: 1.25rem !important;
  }
}
